.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.btn {
  padding: 10px 30px;
  font-size: 30px;
  border: none;
  border-radius: 20px;
  background-color: black;
  color: aliceblue;
  cursor: pointer;
}
.App-header-content {
  display: flex;
  gap: 30px;
}

@media (max-width: 680px) {
  .App-header-content {
    flex-direction: column;
  }
}
